.nav-link {
  font-size: 23px;
  color: #dedede;
  margin-left: 20px;
  margin-right: 20px;
}
.nav-link:after{
  display:block;
  content: '';
  border-bottom: solid 3px #A5C9FF;  
  transform: scaleX(0);  
  transition: transform 250ms ease-in-out;
}

.nav-link:hover {
  color: white;
}
.nav-link:hover:after{
  transform: scaleX(1);
}