.card {
    border-color: linear-gradient(45deg, #A5FFC9,#A5C9FF);
    transition: transform 0.2s ease;
}

.card:hover {
    cursor: pointer;
    transform: scale(1.05);
    border-image-slice: 1;
    animation: rainbow-border 3s linear infinite;
    
}
.nonePointer:hover {
    cursor: default;
}

@keyframes rainbow-border {
    0% {
        border-image: linear-gradient(45deg, #A5FFC9,#A5C9FF);
        border-image-slice: 1;
    }
    100% {
        border-image: linear-gradient(45deg, #A5FFC9,#A5C9FF); 
        border-image-slice: 1;
    }
}